import React, {useState} from "react";
import {Slider} from "./Slider";
import {Price} from "./Price";
import {Feature} from "./Feature";


export const MainContent = () => {
    const [slideNumber, setSlideNumber] = useState<number>(0)
    const array = [
        <div className="d-flex flex-column justify-content-center align-items-center mx-4" style={{height: "30vh"}}>
            <h1> </h1>
            <h6>   <span className="text-decoration-line-through">300</span> 200  .</h6>
        </div>,
        <div className="d-flex flex-column justify-content-center align-items-center mx-4" style={{height: "30vh"}}>
            <h1>  </h1>
            <h6>      1/.</h6>
        </div>,
        <div className="d-flex flex-column justify-content-center align-items-center mx-4" style={{height: "30vh"}}>
            <h1>7   </h1>
            <h6>
                7   .
                <br/>
                   ,      ,   .
                <br/>
                (, , , , , , )
            </h6>
        </div>
    ]

    const show = () => {

        setTimeout(() => {
            if (slideNumber < array.length - 1)
                setSlideNumber(slideNumber + 1)
            else
                setSlideNumber(0)
        }, 4500)

        return array[slideNumber]
    }

    return <>
        {/*{show()}*/}
        <Slider/>
        <Feature/>
        <Price/>
    </>
}
