import React from "react";
import {Button} from "react-bootstrap";
import {useNavigate} from "react-router-dom";

export const Price = () => {
    const navigation = useNavigate()


    return <div className="d-block d-lg-flex justify-content-around w-100 bg-second py-lg-5">
        <div className="d-flex flex-column flex-md-row justify-content-md-around justify-content-center align-items-center flex-fill my-3 my-lg-0">
            <div className="price-box py-2 my-3 my-md-0">
                <div className="pb-2">
                    <h3 className="fw-bold"> 1 </h3>
                </div>
                <div className="pb-2">
                    <div className="pb-2">200/</div>
                    <Button className="bg-second-light w-75 py-3" onClick={() => navigation("/login")}>
                          200
                    </Button>
                </div>
            </div>
            <div className="price-box py-2 my-3 my-md-0">
                <div className="pb-2">
                    <h3 className="fw-bold"> 3 </h3>
                </div>
                <div className="pb-2">
                    <div className="pb-2">180/</div>
                    <Button className="bg-second-light w-75 py-3" onClick={() => navigation("/login")}>
                          540
                    </Button>
                </div>
            </div>
        </div>
        <div className="d-flex flex-column flex-md-row justify-content-md-around justify-content-center align-items-center flex-fill my-3 my-lg-0">
            <div className="price-box py-2 my-3 my-md-0">
                <div className="pb-2">
                    <h3 className="fw-bold"> 6 </h3>
                </div>
                <div className="pb-2">
                    <div className="pb-2">160/</div>
                    <Button className="bg-second-light w-75 py-3" onClick={() => navigation("/login")}>
                          960
                    </Button>
                </div>
            </div>
            <div className="price-box py-2 my-3 my-md-0">
                <div className="pb-2">
                    <h3 className="fw-bold"> 1 </h3>
                </div>
                <div className="pb-2">
                    <div className="pb-2">140/</div>
                    <Button className="bg-second-light w-75 py-3" onClick={() => navigation("/login")}>
                          1680
                    </Button>
                </div>
            </div>
        </div>
    </div>
}